import React, {Fragment} from 'react';
import BigNumber from 'bignumber.js';

import { BalanceBlock } from '../common/index';
import TextBlock from "../common/TextBlock";
import {ownership} from "../../utils/number";

type PoolPageHeaderProps = {
  accountUNIBalance: BigNumber,
  accountBondedBalance: BigNumber,
  accountRewardedESDBalance: BigNumber,
  accountClaimableESDBalance: BigNumber,
  poolTotalBonded: BigNumber,
  accountPoolStatus: number,
  unlocked: number,
  fluidEpoch: any,
  user: string
};

const STATUS_MAP = ["Unlocked", "Locked"];

function status(accountStatus) {
  return STATUS_MAP[accountStatus]
}

const PoolPageHeader = ({
  accountUNIBalance, accountBondedBalance, accountRewardedESDBalance, accountClaimableESDBalance, poolTotalBonded, accountPoolStatus, unlocked, fluidEpoch, user
}: PoolPageHeaderProps) => (
  <div style={{ padding: '2%', display: 'flex', flexWrap: 'wrap', alignItems: 'center' }}>
    <div style={{ flexBasis: '20%' }}>
      <BalanceBlock asset="Balance" balance={accountUNIBalance}  suffix={" Cake-LP"}/>
    </div>
    <div style={{ flexBasis: '20%' }}>
      <BalanceBlock asset="Rewarded" balance={accountRewardedESDBalance} suffix={" SD"} />
    </div>
    <div style={{ flexBasis: '20%' }}>
      <BalanceBlock asset="Claimable" balance={accountClaimableESDBalance} suffix={" SD"} />
    </div>
    <div style={{ flexBasis: '20%' }}>
      <BalanceBlock asset="Pool Ownership" balance={ownership(accountBondedBalance, poolTotalBonded)}  suffix={"%"}/>
    </div>
    <div style={{ flexBasis: '20%' }}>
      <TextBlock label="Pool Status" text={status(accountPoolStatus)}/>
        {
            user !== '' && (
                <Fragment>
                    <p>{
                        isNaN(fluidEpoch)
                            ? 'You did not bonded or unbonded before.'
                            : `You last bonded or unbonded at epoch ${fluidEpoch}.`
                    } </p>
                    {
                        accountPoolStatus !== 0 && (
                            <p>Unlocked at epoch {fluidEpoch + 12}.</p>
                        )
                    }
                </Fragment>
            )
        }
    </div>
  </div>
);


export default PoolPageHeader;
