import React from 'react';
import BigNumber from "bignumber.js";
import {Box} from '@aragon/ui';
import styled from 'styled-components'

type InvestProps = {
  totalSupply: BigNumber,
  totalBonded: BigNumber,
};

const Invest = ({totalSupply, totalBonded}: InvestProps) => {
    const supply = totalSupply.toNumber();
    const bonded = totalBonded.toNumber();
    const dao = (((((supply * (0.75)) / 100) * 60) / 100 + bonded) / bonded);

  return (
      <div style={{fontSize: 16, padding: 3}}>
        <span style={{fontSize: 16, padding: 3}}>APR</span>
        <span style={{fontSize: 16, padding: 3}}>DAO hourly:
          <span style={{
            fontSize: 24,
            padding: 3,
            fontWeight: 400,
            lineHeight: 1.5,
            fontFamily: 'aragon-ui-monospace, monospace'
          }}>
            {((dao - 1) * 100).toFixed(2)}%
          </span>
        </span>
        <span style={{fontSize: 16, padding: 3}}>DAO daily:
          <span style={{
            fontSize: 24,
            padding: 3,
            fontWeight: 400,
            lineHeight: 1.5,
            fontFamily: 'aragon-ui-monospace, monospace'
          }}>
            {(((dao - 1) * 24) * 100).toFixed(2)}%
          </span>
        </span>
        <span style={{fontSize: 16, padding: 3}}>DAO yearly:
          <span style={{
            fontSize: 24,
            padding: 3,
            fontWeight: 400,
            lineHeight: 1.5,
            fontFamily: 'aragon-ui-monospace, monospace'
          }}>
            {((((dao - 1) * 24)*365) * 100).toFixed(2)}%
          </span>
        </span>
      </div>
  );
};

export default Invest;