import React from 'react';
import BigNumber from "bignumber.js";
import {Box} from '@aragon/ui';
import styled from 'styled-components'

type InvestProps = {
  totalSupply: BigNumber,
  ESDLPBonded: BigNumber,
};

const Invest = ({totalSupply, ESDLPBonded}: InvestProps) => {
  const ESDLpBonded = ESDLPBonded.toNumber() * 2;
  const lpHourly = ((((totalSupply.toNumber() * 0.75) / 100) * 40) / 100 + ESDLpBonded) / ESDLpBonded;

  const lpDaily = (((((totalSupply.toNumber() * 0.75) / 100) * 40) / 100) * 24 + ESDLpBonded) / ESDLpBonded;
  const lpWeekly = (((((totalSupply.toNumber() * 0.75) / 100) * 40) / 100) * 8064 + ESDLpBonded) / ESDLpBonded;

  return (
          <div style={{fontSize: 16, padding: 3}}>
            <span style={{fontSize: 16, padding: 3}}>APR</span>
            <span style={{fontSize: 16, padding: 3}}>LP hourly:
              <span style={{
                fontSize: 24,
                padding: 3,
                fontWeight: 400,
                lineHeight: 1.5,
                fontFamily: 'aragon-ui-monospace, monospace'
              }}>
                {((lpHourly - 1) * 100).toFixed(2)}%
              </span>
            </span>
            <span style={{fontSize: 16, padding: 3}}>LP daily:
              <span style={{
                fontSize: 24,
                padding: 3,
                fontWeight: 400,
                lineHeight: 1.5,
                fontFamily: 'aragon-ui-monospace, monospace'
              }}>
                {((lpDaily - 1) * 100).toFixed(2)}%
              </span>
            </span>
            <span style={{fontSize: 16, padding: 3}}>LP yearly:
              <span style={{
                fontSize: 24,
                padding: 3,
                fontWeight: 400,
                lineHeight: 1.5,
                fontFamily: 'aragon-ui-monospace, monospace'
              }}>
                {((lpWeekly - 1) * 100).toFixed(0)}%
              </span>
            </span>
          </div>
  );
};

export default Invest;